.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  font-size: calc(10px + 2vmin);
  color: white;
}

.kinds {
  display: inline-flex;
  width: 100%;
  justify-content: space-around;
  padding: 0;
}
.kinds:nth-child(odd){
  margin-bottom: 0;
}

li {
  list-style: none;
}

.actions {
  padding: 10px 0;
}
button {
  background: #fff;
  border: 0;
}
button:nth-child(odd) {
  margin-right: 30px;
}
